.background {
  width: 430px;
  height: 520px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}

.background .shape {
  height: 200px;
  width: 200px;
  position: absolute;
  border-radius: 50%;
}

.go {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.fb {
  column-gap: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.fb > div {
  &:hover {
    background-color: transparent !important;
  }
  span {
    button {
      font-size: 12px !important;
      color: var(--white_900) !important;
    }
  }
}

.go {
  display: flex !important;

  button {
    background-color: transparent;
    border: none !important;
    margin: 0 !important;
    box-shadow: none !important;
  }

  button > div {
    margin: 0;
    background-color: transparent !important;
  }

  button > span {
    color: #ffffff !important;
  }
}

.shape:first-child {
  background: linear-gradient(#1845ad, #23a2f6);
  left: -80px;
  top: -80px;
}

.shape:last-child {
  background: linear-gradient(to right, #ff512f, #f09819);
  right: -30px;
  bottom: -80px;
}

.formClass {
  height: 490px;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.13);
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
  padding: 29px 35px 0px;
  font-family: "Poppins", sans-serif;
  color: #ffffff;
  letter-spacing: 0.5px;
  outline: none;
  border: none;

  label {
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
  }

  input {
    display: block;
    height: 50px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.07);
    border-radius: 3px;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
    border: none;
  }
  ::placeholder {
    color: #e5e5e5;
  }

  input[type="text"] {
    color: var(--white_900);
  }

  input[type="password"] {
    color: var(--white_900);
  }

  .login {
    width: 100%;
    margin-top: 20px;
    background-color: var(--white_900) !important;
  }
  button {
    width: 100%;
    background-color: transparent !important;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    border: none;
  }

  .social {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
  }

  .social > div {
    border-radius: 3px;
    padding: 5px 10px 10px 5px;
    background-color: rgba(255, 255, 255, 0.27);
    color: #eaf0fb;
    text-align: center;
  }

  .social div:hover {
    background-color: rgba(255, 255, 255, 0.47);
  }
}

.formClass > h3 {
  font-size: 32px;
  font-weight: 500;
  line-height: 42px;
  text-align: center;
}
